<template>
    <view class="wallet_page">
        <!-- tabbar -->
        <cu-custom :refresh="preRrefresh"><block slot="content">我的钱包</block></cu-custom>
        <!-- 头部 -->
        <view class="things_width_full flex justify-start align-start wallet_page_top">
            <!-- 余额 -->
            <view class="flex justify-center align-start balance">
                <view class="balance_text">余额 (元)</view>
                <view class="balance_value">{{userAmount.balance | moneyFormat}}</view>
            </view>
            <!-- 账单明细 -->
            <view class="bill_info">
                <view class="bill_text">账单明细</view>
                <view class="flex justify-start align-center btn_box">
                    <button class="cu-btn" :disabled="buttonType" @tap="linkToRechargeBalance">充值</button>
                    <button class="cu-btn" :disabled="buttonType" @tap="linkToWithDraw">提现</button>
                </view>
            </view>
        </view>
        <!-- 积分 -->
        <view class="things_width_full interests_box">
            <view class="flex justify-between align-center double_box">
                <view class="flex justify-center align-start interests_item" @tap="linkToGiftPoints">
                    <view class="interests_name">馈赠分</view>
                    <view class="flex justify-start align-end interests_value">{{userAmount.gift_points | moneyFormat}}<view class="operate_text" @tap="linkToGiveAway">赠送</view></view>
                </view>
                <view class="flex justify-center align-start interests_item" @tap="linkToConstructionPoints">
                    <view class="interests_name">建设分</view>
                    <view class="flex justify-start align-end interests_value">{{userAmount.build_points | moneyFormat}}<view class="operate_text">挂卖</view></view>
                </view>
            </view>
            <view class="flex justify-between align-center double_box">
                <view class="flex justify-center align-start interests_item" @tap="linkToProfitPoints">
                    <view class="interests_name">营利分</view>
                    <view class="flex justify-start align-end interests_value">{{userAmount.profit_points | moneyFormat}}</view>
                </view>
                <view class="flex justify-center align-start interests_item" @tap="linkToDividend">
                    <view class="interests_name">分红金（元）</view>
                    <view class="flex justify-start align-end interests_value">{{userAmount.bonus_points | moneyFormat}}</view>
                </view>
            </view>
            <view class="flex justify-around align-center single_box">
                <view class="flex justify-center align-center interests_item">
                    <view class="interests_name">建设分</view>
                    <view class="flex justify-start align-end interests_value">{{userAmount.build_points | moneyFormat}}</view>
                </view>
                <view class="flex justify-center align-center interests_item">
                    <view class="interests_name">转化率</view>
                    <view class="flex justify-start align-end interests_value">2.5%</view>
                </view>
                <view class="flex justify-center align-center interests_item">
                    <view class="interests_name">营利分</view>
                    <view class="flex justify-start align-end interests_value">{{userAmount.profit_points | moneyFormat}}</view>
                </view>
            </view>
        </view>
        <!-- 建设分挂卖大厅 -->
        <view class="bg-img bg-mask flex align-center hang_hall" style="background-image:url('./../../../../static/images/global/pointsBg.png')">
            <view class="hang_hall_title">建设分挂卖大厅</view>
        </view>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>
<script>
import { giftsPointsFraction_api,profitPointsList_api } from '@/api/index.js';
import { appData } from '@/utils/config.js';
import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
import { moneyFormat } from '@/utils/filters.js';
import storage from '@/utils/storage.js';
let that = null;
export default {
    data() {
        return {
            preRrefresh: false,
            userAmount: {},// 积分分数
            userData: storage.get("userData") || {},
        }
    },
    filters: {
        moneyFormat
    },
    methods: {
        ...mapMutations([
            'modal',
            'user'
        ]),
        /**清空数据
         * @description: 
         * @param {*} e
         * @return {*}
         */
        clearPageFun(e) {
            that.getFraction();
        },
        /**
        * @description: 跳转到馈赠分
        * @param {*} e
        * @return {*}
        * @author: gitxinghui
        */
        linkToGiftPoints(e) {
            uni.navigateTo({
                url: `/pages/things/integral/giftPoints/info/index`,
                fail: function(err) {
                    console.log(err);
                }
            });
        },
        /**
        * @description: 跳转到建设分
        * @param {*} e
        * @return {*}
        * @author: gitxinghui
        */
        linkToConstructionPoints(e) {
            uni.navigateTo({
                url: `/pages/things/integral/constructionPoints/info/index`,
                fail: function(err) {
                    console.log(err);
                }
            });
        },
        /**
        * @description: 跳转到营利分
        * @param {*} e
        * @return {*}
        * @author: gitxinghui
        */
        linkToProfitPoints(e) {
            uni.navigateTo({
                url: `/pages/things/integral/profitPoints/info/index`,
                fail: function(err) {
                    console.log(err);
                }
            });
        },
        /**
        * @description: 跳转到分红金
        * @param {*} e
        * @return {*}
        * @author: gitxinghui
        */
        linkToDividend(e) {
            uni.navigateTo({
                url: `/pages/things/integral/dividend/info/index`,
                fail: function(err) {
                    console.log(err);
                }
            });
        },
        /** 跳转到充值
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        linkToRechargeBalance(e) {
            uni.navigateTo({
                url: `/pages/things/amount/recharge/balance/index`,
                fail: function(err) {
                    console.log(err);
                }
            });
        },
        /** 跳转到提现
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        linkToWithDraw(e) {
            uni.navigateTo({
                url: `/pages/things/withdraw/index`,
                fail: function(err) {
                    console.log(err);
                }
            });
        },
        /** 跳转到馈赠分赠送页面
         * @description: 
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        linkToGiveAway(e) {
            uni.navigateTo({
                url: `/pages/things/integral/giftPoints/giveAway/index`,
                fail: function(err) {
                    console.log(err);
                }
            });
        },
        /**获取积分分数
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getFraction(e) {
            let params = {};
            giftsPointsFraction_api(params,that).then(res=>{
                that.userAmount = res.userAmount;
            }).catch(err=>{
                console.log(err);
            })

        },
    },
    // 页面生命周期
    onLoad: function (options) {
        that = this;
    },
    onShow: function() {
        that.clearPageFun();
    }
}
</script>

<style lang="scss" scoped>
    @import "./index.scss";
</style>